.ant-btn-primary {
  background-color: #1996cf; }

.ant-layout-header {
  background-color: #e8e8e8; }

.ant-layout-sider {
  background-color: #fff; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.how-my-courses-work {
  margin-top: 100px;
  margin-bottom: 50px; }
  .how-my-courses-work__title {
    text-align: center;
    margin-bottom: 40px; }
    .how-my-courses-work__title h2 {
      color: #39393a;
      font-size: 32px;
      font-weight: bold;
      text-transform: uppercase; }
    .how-my-courses-work__title h3 {
      color: #808080; }
  .how-my-courses-work .row-cards .ant-col {
    padding: 15px !important; }
  .how-my-courses-work__card {
    border: 0;
    background-color: #1a2734;
    border-radius: 10px;
    overflow: hidden;
    text-align: center;
    box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.75); }
    .how-my-courses-work__card .ant-card-body {
      min-height: 215px; }
    .how-my-courses-work__card i {
      font-size: 45px;
      margin-bottom: 20px;
      color: #fff; }
    .how-my-courses-work__card .ant-card-meta-detail .ant-card-meta-title {
      color: #ff8d01;
      font-size: 25px;
      font-weight: bold; }
    .how-my-courses-work__card .ant-card-meta-detail .ant-card-meta-description {
      color: #fff; }
