@import "../../../../scss/index.scss";

.guias-list-web {
  &__list {
    margin-top: 50px;
    margin-bottom: 50px;
  }

  &__title {
    text-align: center;
    text-transform: uppercase;
    font-size: 32px;
    font-weight: bold;
    color: $font-grey;
  }

  &__list {
    .ant-col {
      padding: 15px !important;
    }
  }

  &__card {
    border: 0;
    background-color: $background-dark-light-web;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.75);

    .ant-card-body {
      text-align: center;

      .ant-card-meta-title,
      .ant-card-meta-description {
        color: $font-light;
      }

      .ant-card-meta-title {
        font-weight: bold;
      }
    }

    .ant-card-actions {
      background-color: transparent;
      border: 0;

      button {
        width: 90%;
        height: 35px;
        background-color: transparent;
        color: $font-light;
        border: 1px solid $primary-color;
      }
    }
  }
}
