$defaultFontSize: 16px;

$primary-color: #ff8d01;
$primary-color-hover: #d67b0c;
$primary-color-light: #ffaf01;
$primary-color-dark: #db6f16;
$complementary: rgb(25, 150, 207);

$menu-color: #252527;

$font-light: #fff;
$font-grey-light: #e8e8e8;
$font-grey: #808080;
$font-grey-dark: #000;
$font-dark-light: #39393a;
$font-dark: #000;

$border-light: #fff;
